import React , {Component} from 'react'
import { connect } from 'dva'
import BaseTable from '@/components/BaseTable'
import {
    Button,
    Modal,
    Form,
    Input,
    Select
} from 'antd'

const { Option } = Select;

let mapStateToProps = ({student}) => {
    return {...student}
}

@connect(mapStateToProps)

@Form.create()

class Student extends Component{

    state = {

    }

    getColumns = () => {
        const columns = [{
            title: '姓名',
            dataIndex: 'student_name',
            key: 'student_id',
        }, {
            title: '学号',
            dataIndex: 'student_id',
            key: 'student_id',
        }, {
            title: '班级',
            dataIndex: 'grade_name',
            key: 'grade_id',
        }, {
            title: '教室',
            dataIndex: 'room_text',
            key: 'room_id',
        }, {
            title: '密码',
            dataIndex: 'student_pwd',
            key: 'student_pwd',
        }, {
            title: '操作',
            render: (record) => {
                return <span onClick={() => this.delStudent(record)}>删除</span>
            }
        }];
        return  columns
    }

    delStudent = ({student_id}) => {
        this.props.dispatch({
            type: 'student/delStudent',
            payload: {
                student_id
            }
        })
    }

    componentDidMount() {
        let {
            dispatch
        } = this.props;
        dispatch({
            type: 'student/getAllStudent'
        })
        dispatch({
            type: 'student/getOtherData'
        })
    }

    searchStudent = () => {
        let { validateFields, resetFields } = this.props.form;
        let {
            dispatch
        } = this.props;
        validateFields((err, values) => {
            dispatch({
                type: 'student/searchContent',
                payload: {
                    ...values,
                }
            })
        })
    }

    resetStudent = () => {
        let { resetFields } = this.props.form;
        resetFields()
    }

    changePage = (current, pageSize) => {
        let {
           paginationMsg,
           dispatch,
           allStudents
        } = this.props;
        dispatch({
            type: 'student/changePagination',
            payload: {
                ...paginationMsg,
                current,
                pageSize
            }
        })
    }
    changeSize = (current, pageSize) => {
        let {
           paginationMsg,
           dispatch
        } = this.props;
        dispatch({
            type: 'student/changePagination',
            payload: {
                ...paginationMsg,
                current,
                pageSize
            }
        })
    }

    render(){
        let {
            allStudents,
            paginationMsg,
            allRoom,
            allSubject,
            form: {
                getFieldDecorator
            }
        } = this.props;
        return <div>
             <Form layout="inline">
                <Form.Item>
                    {getFieldDecorator('student_name')(
                        <Input placeholder="输入学生姓名" />
                    )}
                </Form.Item>
                <Form.Item>
                        {getFieldDecorator('room_id')(
                            <Select placeholder="请选择教室号" style={{ width: '180px' }}>
                                {
                                    allRoom.length > 0 && allRoom.map((val, ind) => {
                                        return <Option value={val.room_id} key={val.room_id}>{val.room_text}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item>
                    {/* <Form.Item>
                        {getFieldDecorator('subject_id')(
                            <Select placeholder="班级名" style={{ width: '180px' }}>
                                {
                                    allSubject.length > 0 && allSubject.map((val, ind) => {
                                        return <Option key={val.subject_id} value={val.subject_id}>{val.subject_text}</Option>
                                    })
                                }
                            </Select>
                        )}
                    </Form.Item> */}
                <Form.Item>
                    <Button type="primary" htmlType="submit" onClick={this.searchStudent}>搜索</Button>
                </Form.Item>
                <Form.Item>
                    <Button type="primary" htmlType="submit" onClick={this.resetStudent}>重置</Button>
                </Form.Item>
            </Form>
            <BaseTable
                rowKey={record => record.student_id}
                dataSource={allStudents}
                columns={this.getColumns()}
                pagination={false}
                wrapStyle={{
                    padding: 0,
                }}
                pagination={{
                    ...paginationMsg,
                    onChange: this.changePage,
                    onShowSizeChange: this.changeSize
                }}
            />
        </div>
    }
}
export default Student;